@import "./vars";
@import "./mixins";


body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1568px;
    margin: 0 auto;
    padding: 0 15px;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

// .grid-menu {
//     display: grid;
//     grid-template-columns: repeat(8, 1fr);
// }

a {
    text-decoration: none;
}

img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.input {
    background: rgba(7, 7, 7, 0.03);
    padding-left: 43px;
    border-radius: 3px;
    width: 100%;
    outline: none;
    height: 41px;
    transition: all .3s ease-out;
}

.btn {
    background-color: $tuliptree;
    box-shadow: 0px 4px 20px rgba(232, 170, 49, 0.43);
    border-radius: 3px;
    outline: none;
    cursor: pointer;
    color: #fff;
    transition: opacity 0.3s ease-in-out;
    border: none;

    &:hover {
        opacity: 0.9;
    }
}

.title {
    font: $bold-book-high;
    letter-spacing: 0.05em;
    color: $codgray;
}

.sub-title {
    color: $codgray;
    font: $middle-book-higher;
    text-transform: capitalize;
}

.btn-yellow {
    background-color: $tuliptree;
    color: #fff;
    width: 100%;
    box-shadow: 0px 4px 20px rgba(232, 170, 49, 0.43);
    border-radius: 3px;
    outline: none;
    border: none;
    font: $middle-book-high;
    padding: 17px 0;
    text-align: center;
    cursor: pointer;

    &::after {
        content: "";
        background-image: url('../img/cart-white.svg');
        width: 19px;
        height: 19px;
        position: absolute;
        margin-left: 15px;
    }
}

.catalog {
    font: $middle-book-high;
    letter-spacing: 0.05em;
    color: rgba(20, 20, 20, 0.58);
    position: relative;

    &::before {
        content: "";
        background-image: url('../img/sprite.svg#arrow');
        background-repeat: no-repeat;
        position: absolute;
        top: 6px;
        right: -27px;
        transform: rotate(270deg);
        width: 16px;
        height: 10px;
    }
}

.text-above {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

